body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: stretch;
}

.navigation {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 4px #d3d3d3;
  margin-bottom: 20px;
}

.navigation__list {
  display: flex;
  justify-content: flex-end;

  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation__logo-container {
  display: flex;
  align-items: center;
  max-width: 150px;

  margin-left: 20px;
}

.navigation__logo {
  min-height: 2em;
  transition: all 0.5s;
}

.navigation__link {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  min-height: 40px;

  color: #000;
  background-color: #fff;

  text-decoration: none;
}

.navigation__link:hover {
  background-color: rgb(235, 235, 235);
}

.navigation__basket-quatity {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8em;
  color: #fff;

  position: relative;
  top: -6px;

  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #12a34a;
}

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  justify-self: flex-end;

  box-sizing: border-box;
  min-height: 60px;
  padding: 20px;
  margin: 0;
  margin-top: auto;

  box-shadow: 0 0 4px #d3d3d3;
  background-color: #fff;
}

.footer a {
  text-decoration: none;
  color: #c4c0d1;

  transition: all 0.5s;
}

.footer a:hover {
  color: #826bcf;
}

.home__header {
  margin: 30px 0 46px;
  padding: 0;
  font-size: 3em;
}

.home__description {
  position: relative;
  color: #9595c7;
  line-height: 1.6em;
  margin: 20px 0 50px;
  padding: 0;
}

.home__description::before {
  content: '';
  position: absolute;
  top: -1.8em;
  left: 0;

  width: 70px;
  height: 4px;
  background-color: #0000ff;
}

.home__image-container img {
  width: 100%;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  box-sizing: border-box;
  margin: 0 auto;
  padding: 20px;
  width: 100%;
  max-width: 1300px;
}

.main-container--home {
  display: grid;
  grid-template-columns: minmax(auto, 500px) auto;
}

.active {
  color: #fff;
  background-color: #0000ff;
}

.active:hover {
  color: #fff;
  background-color: #0000ff;
}

.phone__main-info {
  display: grid;
  grid-template-columns: auto minmax(400px, 1fr);
}

.phone__galary{
  margin-right: 40px;
}

.phone__image {
  position: relative;
  overflow: hidden;

  padding: 20px;
  margin-bottom: 10px;
  max-width: 400px;
  border: 1px solid #e7e7e7;
}

.phone__image img {
  width: 100%;
}

.phone__image-out {
  display: none;
}

.phone__image-animation--in {
  animation-duration: 0.8s;
  animation-name: slidein;
}

.phone__image-animation--out {
  animation-duration: 0.8s;
  animation-name: slideout;
}

@keyframes slidein {
  from {
    transform: translateX(-300px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.phone__preview {
  width: 30px;
  border: 1px solid black;
  margin-right: 10px;
  padding: 10px;
  background-color: #fff;

  cursor: pointer;
  transition: all 0.6s;
}

.phone__card-list {
  display: grid;
  grid-template-columns: repeat(5, minmax(200px, 300px));
  justify-content: center;

  list-style: none;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  width: 100%;
}

.phone__card-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  position: relative;
  box-sizing: border-box;
  margin: 10px;
  padding: 20px;

  box-shadow: 0 0 10px #d3d3d3;

  transition: all 1s;
  animation: cardItem 1s ease-in-out;
}

@keyframes cardItem {
  from {
    opacity: 0;
    transform: scale(0.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.phone__card-header a {
  margin: 0;
  padding: 10px 0;
  color: #333;
  text-decoration: none;
}

.phone__card-header:hover a {
  color: #3232e2;
}

.phone__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.basket__card-list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.basket__card-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  box-sizing: border-box;
  margin: 20px 0;
  padding: 20px;
  width: 100%;

  box-shadow: 0 0 10px #d3d3d3;
}

.basket__item-header {
  text-decoration: none;
  font-size: 1em;
}

.basket__item-header a {
  text-decoration: none;
  font-size: 1em;
}

.basket__preview-name-container {
  display: flex;
  align-items: center;
}

.basket__card-image {
  width: 60px;
  margin-right: 20px;
}

.basket__card-image img {
  width: 100%;
}

.basket__item-quantity {
  margin-left: 20px;
}

.basket__controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.basket__controls--center {
  display: flex;
  justify-content: center;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 15px;
  color: #fff;
  background-color: #3232e2;
  border: none;
  font: inherit;
  font-size: 0.9rem;

  text-transform: uppercase;
  text-decoration: none;
}

.button--home {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 160px;
  margin-bottom: 10px;
}

.button--basket {
  min-width: 50px;
  margin-left: 20px;
}

.button--basket-delete {
  padding: 5px;
  margin-left: 20px;

  font-size: 0.6em;
  width: 24px;
  max-height: 28px;

  color: #000;
  background-color: #eeeefc;
}

.button--basket-disabled {
  pointer-events: none;
  color: #000;
  background-color: #f5f5f5;
}

.button--delete-all-items {
  background-color: #fff;
  color: #2222db;
  box-shadow: 0 0 5px #dfdfdf;
}

.button:hover {
  color: #fff;
  background-color: #2222db;
  cursor: pointer;
}

.button--added {
  background-color: rgb(19, 177, 80);
}

.button--added-phone-page {
  margin-right: 20px;
}

.button--added:hover {
  background-color: rgb(19, 177, 80);
}

.button--basket-delete:hover {
  background-color: #ffffff;
  color: #db2241;
  box-shadow: 0 0 10px #ffb3b3;
}

.button--delete-all-items:hover {
  background-color: #fff;
  color: #db2241;
  box-shadow: 0 0 10px #ffb3b3;
}

.button--pugination {
  color: #000;
  background-color: #ffffff;
  box-shadow: 0 0 5px #dfdfdf;
  min-width: 50px;

  margin: 0 10px;
}

.button--pugination-active {
  color: #fff;
  background-color: #3232e2;
}

.button--pugination-disabled {
  pointer-events: none;
  color: #000;
  background-color: #f5f5f5;
  box-shadow: none;
}

.catalog__sort-filter-container {
  display: grid;
  grid-template-columns: 6fr 2fr 1fr;
  grid-column-gap: 20px;
  width: 100%;

  margin: 20px;
  padding: 0 10px;
  box-sizing: border-box;
}

.catalog__filter {
  padding: 10px;
  border: none;
  box-shadow: 0 0 4px #e9e9e9;
}

.catalog__filter:hover {
  box-shadow: 0 0 8px #dfdfdf;
}

.catalog__sort-select {
  padding: 10px;
  border: none;
  box-shadow: 0 0 4px #e9e9e9;
}

.catalog__items-per-page {
  padding: 10px;
  border: none;
  box-shadow: 0 0 4px #e9e9e9;
}

.catalog__sort-select:hover {
  box-shadow: 0 0 8px #dfdfdf;
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 100%;
}

.pagination__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-wrap: wrap;
}

.pagination__description {
  margin-left: 10px;
}

.nophone__link-list {
  display: flex;
  justify-content: space-between;

  list-style: none;
  margin: 0;
  padding: 0;
}

.nophone__image {
  margin-bottom: 20px;
}

.phone__details {
  width: 100%;
}

.phone__details-list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;
  margin: 0;
  padding: 0;
}

.phone__details-list h4 {
  margin: 10px 0;
  padding: 0;
}

.phone__details-item {
  margin-right: 20px;
  min-width: 120px;
  max-width: 160px;
}

.phone__details-item dl {
  margin: 0;
  padding: 0;
  font-size: 0.9rem;
}

.phone__details-item dt {
  font-weight: bold;
}

.phone__details-item dd {
  margin: 5px 0 10px;
  padding: 0;
}

.phone__detail-availabilty {
  width: 14px;
}

.phone__buttons {
  display: flex;
  justify-content: flex-start;
}

.rights-list {
  list-style: none;
  margin: 0;
  padding: 0;

  text-align: center;
}

.rights-list li {
  margin-bottom: 10px;
}

.rights-list a {
  text-decoration: none;
}

.rights-list a:hover {
  color: #3232e2;
}

@media (max-width: 1200px) {
  .phone__card-list {
    grid-template-columns: repeat(4, minmax(200px, 300px));
  }
}

@media (max-width: 1040px) {
  .phone__card-list {
    grid-template-columns: repeat(3, minmax(180px, 380px));
  }
}

@media (max-width: 780px) {
  .phone__card-list {
    grid-template-columns: repeat(2, minmax(180px, 380px));
  }

  .main-container--home {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 740px) {
  .phone__main-info {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 540px) {
  .phone__card-list {
    grid-template-columns: repeat(1, minmax(180px, 460px));
  }

  .button--pugination {
    min-height: 25px;
    min-width: 25px;
    margin-bottom: 20px;
  }

  .navigation__link {
    padding: 10px;
  }

  .catalog__sort-filter-container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2;
    grid-row-gap: 15px;
  }

  .catalog__filter {
    margin-right: 0;
    grid-row-start: 1;
    grid-column: 1 / 3;
  }

  .basket__card-item {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 460px) {
  .navigation__logo-container {
    max-width: 80px;
  }

  .navigation__logo {
    min-height: 1.7em;
  }
}
